.loader__container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
  background-color: #f2f2f2;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.loader__title {
  color: #262223;
  font-style: 2rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
  -webkit-animation: colorflow 1.3s infinite ease;
          animation: colorflow 1.3s infinite ease;
}

@-webkit-keyframes colorflow {
  0% {
    color: #262223;
  }
  100% {
    color: #f2811d;
  }
}

@keyframes colorflow {
  0% {
    color: #262223;
  }
  100% {
    color: #f2811d;
  }
}

.loader__cubegrid {
  width: 40px;
  height: 40px;
}

.loader__cube {
  width: 33%;
  height: 33%;
  background-color: #262223;
  float: left;
  -webkit-animation: cubegridScaleDelay 1.3s infinite ease-in-out;
          animation: cubegridScaleDelay 1.3s infinite ease-in-out;
}

.loader__cube1 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.loader__cube2 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.loader__cube3 {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

.loader__cube4 {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.loader__cube5 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.loader__cube6 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.loader__cube7 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.loader__cube8 {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.loader__cube9 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

@-webkit-keyframes cubegridScaleDelay {
  0%,
  70%,
  100% {
    transform: scale3d(1, 1, 1);
  }
  35% {
    transform: scale3d(0, 0, 1);
    background-color: #f2811d;
  }
}

@keyframes cubegridScaleDelay {
  0%,
  70%,
  100% {
    transform: scale3d(1, 1, 1);
  }
  35% {
    transform: scale3d(0, 0, 1);
    background-color: #f2811d;
  }
}

.navbar__container {
  width: 100%;
  position: fixed;
  background-color: #262223;
  top: 0;
  left: 0;
  display: flex;
  padding: 25px 100px;
  justify-content: space-between;
  align-items: center;
  transition: 0.6s;
}

.navbar__logo {
  position: relative;
  text-transform: capitalize;
  font-weight: 600;
  font-size: 2rem;
  padding: 0 10px;
  color: #f2f2f2;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.6s;
}

.navbar__menu {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}

.navbar__menuitem {
  position: relative;
  color: #f2f2f2;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0 10px;
  outline: none;
  font-weight: 600;
  transition: 0.6s;
  letter-spacing: 2px;
  margin: 0 15px;
}

.navbar__menuitem--active {
  color: #f2811d;
  border-bottom: 2px solid #f2811d;
}

.navbar__showmenubtn {
  padding: 0 10px;
}

.navbar__showmenubtn,
.navbar__closemenubtn {
  transition: 0.6s;
  color: #f2f2f2;
  font-size: 2em;
  cursor: pointer;
  display: none;
}

.navbar__showbtn {
  position: relative;
  top: 5px;
}

.navbar__menuitem:hover,
.navbar__showmenubtn:hover,
.navbar__closemenubtn:hover {
  color: #f2811d;
}

#navbar-chk {
  position: absolute;
  visibility: hidden;
  z-index: -1111;
}

.navbar__container--sticky {
  padding: 5px 100px;
  background: #262223;
  opacity: 1;
  z-index: 100;
}

@media screen and (max-width: 812px) {
  .navbar__container {
    padding: 15px 10px;
    z-index: 100;
  }

  .navbar__showmenubtn,
  .navbar__closemenubtn {
    display: block;
    transition: 0.6s;
  }

  .navbar__menu {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: #262223;
    right: -100%;
    top: 0;
    text-align: center;
    padding: 100px 0;
    line-height: normal;
    transition: 0.6s;
    opacity: 0.986;
    flex-flow: column nowrap;
  }

  .navbar__menuitem {
    display: block;
    color: #f2f2f2;
    padding: 20px;
    font-size: 2rem;
    transition: 0.6s;
  }

  .navbar__container--sticky {
    padding: 5px 10px;
  }

  .navbar__menuitem--active {
    color: #f2811d;
    border: none;
  }

  .navbar__closemenubtn {
    position: absolute;
    top: 30px;
    right: 30px;
  }

  .navbar__menu-open {
    right: 0;
  }

  .navbar__showmenubtn-display {
    opacity: 0;
  }
}

